import { Injectable } from '@angular/core';
import { AdaptButtonComponent } from '@bmc-ux/adapt-angular';

import { ActionButtonIconAlignment, ActionButtonStyle } from './action-button.types';

@Injectable()
export class RxActionButtonService {
  getButtonType(style: ActionButtonStyle): AdaptButtonComponent['type'] {
    let result: AdaptButtonComponent['type'];

    switch (style) {
      case ActionButtonStyle.Secondary:
        result = 'secondary';
        break;
      case ActionButtonStyle.Tertiary:
        result = 'tertiary';
        break;
      default:
        result = 'primary';
        break;
    }

    return result;
  }

  getIconCssClass(iconClass: string, iconAlignment = ActionButtonIconAlignment.Left): string {
    return iconClass ? `d-icon-${iconAlignment}-${iconClass}` : '';
  }
}
